.movies .movie {  /** changing this = compare to just .movies = impact all the content execpt img = as img = not part of <div className="movie"> = FK IDK NOW, what is .movies actually referring to?*/
  width: 45%; /*? if this is 45%, but inc. img = 60%, does that mean width of img = 60 - 45 = 15%? */
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px; /*? seem to only affect LHS? well more like RHS margin = is off the bg-color = due to special margin-right value from .movie img*/
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  /*! box-shadow w/border-radius set = box shadow takes on same rounded corners*/
}

.movie img {
  position: relative;
  top: -50px; /** make img stick upward*/
  max-width: 150px; /** this is affecting size of img*/
  width: 100%;
  margin-right: 30px; /** this may look like it ain't doing nothing, but bcoz this is margin = u can see ur text moving more towards right */
  border: 2px solid red;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

/* 296px - (30 + 2 + 2) -  116 - 20 - 20 = 106 - */
/* 1263 * 0.45 = 568.35, 296 x 2 = 592...*/

.movie .movie__title,
.movie .movie__year {
  margin: 0; /** affect margin of title & year */
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px; /** affect dist. b/w title & year */
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genres {
  list-style: none;
  padding: 0; /** remove padding, so it aligns list of genres to title, summary = i.e. all texts*/
  margin: 0; /*? only affect top dist? b/w genres & year? */
  display: flex;
  margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
  margin-right: 10px;
  font-size: 14px; /*! altho. movie__year = <h5> = 0.83em font-size, i thought it was easier to just set a standard font-size in px*/
}
